@import url("https: //fonts.googleapis.com/css2?family=Jost:wght@300&display=swap");
*{
    padding: 0%;
    margin: 0%;
    font-family: "Jost",sans-serif;
    
    
}

body{
    background-color: #f3f2ef;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    height: 100vh;
    width: 100vw;
    font-family: "Jost,sans-serif";

}

html{
    font-size: 100%;
    height: 100%;

}
html{
    background: #eee;
}
.box{
    width: 25vw;
    height: 60vh;
    border-radius: 0.5rem;
    box-shadow: 0 0.2rem 3rem rgba(0,0,0,0.2);
    background: #a5bbdd;
    position: relative;
    overflow: hidden;
    transform: translate3d(0,0,0);
    min-width: 20rem;
    min-height: 35rem;
}

.wave{
    opacity: 0.3rem;
    position: absolute;
    top: 120%;
    left: 50%;
    background: white;
    width: 50rem;
    height: 50rem;
  
  
    margin-left: -25rem;
    margin-top: -20rem;
transform-origin: 50% 48%;
border-radius: 43%;
animation: drift 3000ms infinite linear;
z-index: 1;

}
.wave-three{
animation: drift 5000ms infinite linear;
z-index: 2!important;
opacity: 0.2;
}

.wave-two{
    animation: drift 7000ms infinite linear;
    z-index: 3!important;
    opacity: 0.1;
}


.wave{
    top: 85%;

}

.info{
    font-size: 1.5rem;
    text-align: center;
    display: inline;
   
}


.box::after{
    content: "";
    display: block;
    left: 0%;
    top: 0%;
    width: 100%;
    height: 100%;
    z-index: 11;
    transform: translate3d(0,0,0);
}

@keyframes drift {
    from
    {
        transform: rotate(0deg);
    }
    from{
        transform: rotate(360deg);
    }
    
}

.inputData{
    
    margin-top: 80px;
    margin-left: 80px;
    
    
}
.inputField{
    width: 50%;
    margin: auto;
    border-radius: 5rem;
    padding: 1rem 1rem;
    outline: none;
    font-size: 1.5rem;

}

.inputData{
    width: 100%;
    height: 10vh;
   margin-left: 7rem;
    justify-content: center;
    align-items: center;
   
}


.tempmin_max{
    font-weight: 200;
    color:rgba(27, 4, 76, 0.926);
}

.errorMsg{
    text-align: center;
    font-size: 2rem;
    margin: 2rem auto;
    text-decoration: dotted;
}

.location{
 text-transform: capitalize;
 height: 2rem;
 font-weight: 500;
 
}
.temp{
    font-weight: 500;
}